import { useRouter } from 'next/router';

const tools = [
  { id: 'op20', name: 'OP20 Tool Correction', description: 'Generate MMK program for OP20' },
  { id: 'op10', name: 'OP10 Tool Correction', description: 'Generate MMK program for OP10' },
];

export default function Home() {
  const router = useRouter();

  return (
    <div className="flex flex-col items-center p-6">
      <h1 className="text-3xl font-bold mb-6">CNC Tool Generator</h1>
      <p className="mb-4 text-gray-700">Select a tool correction to generate an MMK program.</p>
      <div className="w-full max-w-md">
        {tools.map((tool) => (
          <button
            key={tool.id}
            onClick={() => router.push(`/tool/${tool.id}`)}
            className="w-full bg-blue-500 text-white px-4 py-3 mb-3 rounded-md hover:bg-blue-600 transition"
          >
            {tool.name}
          </button>
        ))}
      </div>
    </div>
  );
}
